import { Component, Element, h, Host, Method, Prop, State } from '@stencil/core';
import { SharedStyles } from '@utils/shared-styles';
import { Autoplay, Swiper, SwiperOptions } from 'swiper';

@Component({
  tag: 'storm-swipe',
  styleUrl: 'storm-swipe.css',
  shadow: true,
})
export class StormSwipe {
  @Element() host: HTMLElement;
  @Prop() options: SwiperOptions;
  @Prop() width: number = 800;
  @Prop() height: string = '25px';

  @State() swiper?: Swiper;
  @State() swiperRef?: HTMLDivElement;

  // @Prop() timingFunction: string;

  @Method()
  async loopDestroy() {
    this.swiper.loopDestroy();
  }

  @Method()
  async loopCreate() {
    this.swiper.loopCreate();
  }

  @Method()
  async autoplayStop() {
    this.swiper.autoplay.stop();
  }

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');
  }

  componentDidLoad() {
    // this.options.width = this.width;
    if (this.swiperRef) {
      Swiper.use([Autoplay]);
      this.swiper = new Swiper(this.swiperRef, this.options);
    }
  }

  // setTextContent() {
  //   return this.host.textContent
  //   return <slot></slot>
  // }

  video = {
    name: 'test1',
    url: 'https://media.w3.org/2010/05/sintel/trailer.mp4',
    poster: 'https://media.w3.org/2010/05/sintel/poster.png',
  };

  render() {
    return (
      <Host>
        <div class="swiper" ref={el => (this.swiperRef = el)} /*style={{width:this.width, height:this.height}}*/>
          <div class="swiper-wrapper">
            <div class="swiper-slide">
              {/* <slot/> */}
              <img src="https://media.w3.org/2010/05/sintel/poster.png"></img>
              {/* {this.setTextContent()} */}
            </div>
            <div class="swiper-slide">
              <img src="//vjs.zencdn.net/v/oceans.png"></img>
            </div>
          </div>

          {/* <div class="swiper-pagination"></div> */}
          {/* <div class="swiper-button-prev"></div> */}
          {/* <div class="swiper-button-next"></div> */}
          {/* <div class="swiper-scrollbar"></div> */}
        </div>
      </Host>
    );
  }
}
